@charset "UTF-8";

/* Slider */

.slick-loading .slick-list {
  background: #fff url('./ajax-loader.gif') center center no-repeat;
}

/* Arrows */

.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  height: 20px;
  width: 20px;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 50%;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -50%);
  padding: 0;
  border: none;
  outline: none;
}

.slick-prev:hover,
.slick-next:hover,
.slick-prev:focus,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: 0.25;
}

.slick-prev:before,
.slick-next:before {
  font-family: "slick";
  font-size: 20px;
  line-height: 1;
  color: white;
  opacity: 0.75;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* Icons */
}

@font-face {
  font-family: 'slick';
  font-weight: normal;
  font-style: normal;
  src: url('./fonts/slick.eot');
  src: url('./fonts/slick.eot?#iefix') format('embedded-opentype'), url('./fonts/slick.woff') format('woff'), url('./fonts/slick.ttf') format('truetype'), url('./fonts/slick.svg#slick') format('svg');
}

.slick-prev {
  left: -25px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -25px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -25px;
}

[dir="rtl"] .slick-next {
  left: -25px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */

.slick-dotted .slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: -25px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  margin: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  height: 20px;
  width: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  border: 0;
  background: transparent;
  display: block;
  height: 20px;
  width: 20px;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: transparent;
  padding: 5px;
  cursor: pointer;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  position: absolute;
  top: 0;
  left: 0;
  content: "•";
  width: 20px;
  height: 20px;
  font-family: "slick";
  font-size: 6px;
  line-height: 20px;
  text-align: center;
  color: black;
  opacity: 0.25;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  color: black;
  opacity: 0.75;
}

/* Slider */

.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.slick-track:before,
.slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-arrow.slick-hidden {
  display: none;
}

/* Set defaults */

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html,
body {
  box-sizing: border-box;
}

*:focus {
  outline: none;
  box-shadow: 0 0 20px 3px rgba(255, 0, 0, 0.3);
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: 'Montserrat', 'Open Sans', 'Arial', sans-serif;
  max-width: 1920px;
  color: #fff;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.2;
  background-color: #121314;
}

::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  background-color: #222;
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #c50c0c;
}

img,
video {
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

/* Atomic */

.d-n {
  display: none;
}

.d-b {
  display: block;
}

.d-f {
  display: flex;
}

.ttu {
  text-transform: uppercase;
}

.ta-c {
  text-align: center;
}

.ov-h {
  overflow: hidden;
}

.ov-s {
  overflow: scroll;
}

.black {
  color: #000;
}

/* Global */

.wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.col {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
  margin-right: 10px;
}

.sr-only,
.screen-reader-text {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  appearance: none;
  display: inline-block;
  background-color: #c50c0c;
  color: #fff;
  border: none;
  text-align: center;
  font-weight: 600;
  font-size: 18px;
  padding: 25px 40px;
  text-transform: uppercase;
  line-height: 1;
}

.btn:hover,
.btn:active,
.btn:focus {
  background-color: #ae0b0b;
  transition: background-color 0.5s;
}

.main-heading,
.page-heading {
  font-size: 48px;
  font-weight: 900;
  color: #fff;
  text-transform: uppercase;
}

.main-heading_404 {
  padding-top: 200px;
  padding-bottom: 200px;
  text-align: center;
  font-size: 6vw;
}

.page-heading {
  font-size: 36px;
}

.link-more {
  font-weight: 600;
  font-size: 16px;
  padding-right: 35px;
  display: inline-block;
  color: #c50c0c;
  background-image: url(../img/icons/arrow_red.png);
  background-size: 21px 10px;
  background-repeat: no-repeat;
  background-position: right center;
}

.link-more:hover,
.link-more:active,
.link-more:focus {
  color: #fff;
  background-image: url(../img/icons/arrow_white.png);
}

.link-more_inverse {
  background-image: url(../img/icons/arrow_black.png);
}

.price {
  font-size: 48px;
  font-weight: 800;
  color: #fff;
}

.price__unit {
  color: #999999;
  font-size: 18px;
  font-weight: 400;
}

.inner .main-content {
  background-image: url(../img/inner_bg.jpg);
  background-position: top center;
  background-attachment: fixed;
  background-size: cover;
  background-repeat: no-repeat;
  padding-top: 140px;
  padding-bottom: 140px;
}

.inner .cards {
  background-image: unset;
}

.inner .cards__h {
  margin-bottom: 45px;
}

.like {
  padding-left: 20px;
  background-image: url(../img/svg/like.svg);
  background-size: 15px;
  background-position: left center;
  background-repeat: no-repeat;
  position: relative;
}

.like svg {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 0%;
  transform: translateY(-50%);
}

.like path {
  fill: #666;
}

.like:hover,
.like:active,
.like:focus,
.like.like_liked {
  color: #fff;
}

.like:hover path,
.like:active path,
.like:focus path,
.like.like_liked path {
  fill: #fff;
}

/* General */

.main-header {
  padding-top: 20px;
  background-color: transparent;
  height: 100px;
  margin-bottom: -100px;
  position: relative;
  z-index: 2;
}

.main-header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.main-header__logolink,
.main-header__logolink img {
  display: block;
}

.main-header__widget {
  margin-left: 30px;
}

.main-header__widget.active {
  display: block;
  position: absolute;
  z-index: 999999;
  left: 50%;
  transform: translateX(-50%);
  top: 10px;
}

.main-header__mobile {
  display: none;
  width: 25px;
  height: 25px;
  border: none;
  background-color: transparent;
  border-top: 2px solid #fff;
  position: relative;
  z-index: 999999;
}

.main-header__mobile:before,
.main-header__mobile:after {
  content: '';
  display: block;
  position: absolute;
  left: 0;
  width: 100%;
  border-top: 2px solid #fff;
}

.main-header__mobile:before {
  top: 50%;
  margin-top: -2px;
}

.main-header__mobile:after {
  bottom: 0;
}

.main-header__mobile:hover {
  border-top-color: #c50c0c;
  transition: 0.5s;
}

.main-header__mobile:hover:before,
.main-header__mobile:hover:after {
  border-top-color: #c50c0c;
  transition: 0.5s;
}

.main-header__mobile.closer {
  border-top-color: transparent;
}

.main-header__mobile.closer:after {
  top: 50%;
  margin-top: -2px;
  transform-origin: 12.5px 1px;
  transform: rotate(45deg);
}

.main-header__mobile.closer:before {
  transform: rotate(-45deg);
}

.main-navigation {
  margin: 0 10px;
}

.main-navigation.active {
  display: block;
  position: fixed;
  z-index: 999998;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.9);
  overflow: auto;
}

.main-navigation.active .main-navigation__list {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  top: 20%;
}

.main-navigation.active .main-navigation__list li {
  margin: 0;
}

.main-navigation.active .main-navigation__list li + li {
  margin: 0;
  margin-top: 20px;
}

.main-navigation__list {
  display: flex;
  justify-content: space-between;
  list-style: none;
  align-items: baseline;
}

.main-navigation__list li {
  padding-bottom: 10px;
  border-bottom: 1px solid transparent;
}

.main-navigation__list li + li {
  margin-left: 30px;
}

.main-navigation__list a {
  color: #fff;
}

.main-navigation__list li.active,
.main-navigation__list li.current-menu-item,
.main-navigation__list li:hover {
  border-bottom-color: #c50c0c;
}

.inner .main-header {
  border-bottom: 1px solid #131313;
}

.widget-contacts {
  display: block;
}

.widget-contacts__phone {
  font-weight: 500;
  line-height: 1;
  color: #fff;
  font-size: 24px;
  display: block;
  padding-top: 10px;
  padding-bottom: 5px;
  padding-right: 35px;
  background-image: url(../img/svg/phone.svg);
  background-size: 25px 25px;
  background-repeat: no-repeat;
  background-position: right center;
}

.widget-contacts__phone:hover,
.widget-contacts__phone:focus,
.widget-contacts__phone:active {
  color: #777;
