  padding: 0;
  text-align: center;
  color: #999999;
  font-size: 12px;
}

.footer .widget-contacts__phone {
  padding-right: 0;
  padding-left: 35px;
  background-position: left center;
}

.widget-contact-mail {
  font-size: 12px;
}

.widget-contact-mail a {
  color: #999999;
  text-decoration: underline;
}

.widget-contact-mail a:hover,
.widget-contact-mail a:focus,
.widget-contact-mail a:active {
  color: #fff;
  transition: color 0.5s;
}

.widget-social-links {
  display: inline-block;
  vertical-align: middle;
  width: 36px;
  height: 36px;
  border: 1px solid #18191b;
  position: relative;
}

.widget-social-links svg {
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.widget-social-links + .widget-social-links {
  margin-left: 15px;
}

.widget-social-links:hover path,
.widget-social-links:focus path,
.widget-social-links:active path {
  fill: #c50c0c;
  transition: fill 0.5s;
}

.widget-address,
.widget-working-time,
.widget-phone,
.widget-email {
  font-size: 16px;
  color: #cccccc;
  padding-left: 30px;
  position: relative;
}

.widget-address:after,
.widget-working-time:after,
.widget-phone:after,
.widget-email:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 25px;
  height: 25px;
  border: none;
  border-radius: 50%;
  background-color: #c50c0c;
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: center;
}

.widget-address:after {
  background-image: url(../img/svg/placeholder-for-map.svg);
}

.widget-working-time:after {
  background-image: url("../img/svg/clock (2).svg");
}

.widget-phone:after {
  background-image: url(../img/svg/smartphone.svg);
}

.widget-email:after {
  background-image: url(../img/svg/mail.svg);
}

.footer {
  padding-top: 50px;
  padding-bottom: 25px;
  background-color: #121314;
}

.footer .main-header {
  margin-bottom: 0;
  padding-top: 5px;
  padding-bottom: 35px;
}

.footer .main-header__wrap {
