}

.main-footer {
  border-top: 1px solid #1f2021;
  padding-top: 35px;
}

.main-footer__row {
  align-items: center;
}

.main-footer__widget {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 10px;
