}

.main-footer__widget_social {
  text-align: right;
}

.modal {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.8);
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 9998;
  display: none;
}

.modal.active {
  display: block;
}

.modal .wrapper {
  position: relative;
  height: 100vh;
}

.modal__closer {
  position: absolute;
  top: 10px;
  right: 10px;
  border: none;
  background-color: transparent;
  display: block;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.modal__closer:before,
.modal__closer:after {
  content: '';
  display: block;
  position: absolute;
  border-bottom: 1px solid #fff;
  top: 50%;
  width: 100%;
  left: 0;
}

.modal__closer:before {
  transform: rotate(45deg);
}

.modal__closer:after {
  transform: rotate(-45deg);
}

.modal__closer:hover:before,
.modal__closer:focus:before,
.modal__closer:active:before,
.modal__closer:hover:after,
.modal__closer:focus:after,
.modal__closer:active:after {
  border-bottom-color: #c50c0c;
  transition: border-bottom-color 0.5s;
}

.modal-content {
  box-shadow: 0 0 15px 3px #777;
  padding: 40px 10px;
  position: absolute;
  z-index: 99999;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  min-width: 300px;
  width: 90%;
  max-width: 800px;
  overflow: auto;
  background-color: #000;
}

.modal-content__h {
  font-weight: 800;
  font-size: 24px;
  margin-bottom: 30px;
}

.modal-form__form {
  text-align: center;
}

.modal-form__form p {
  margin-bottom: 20px;
}

.modal-form__form input {
  width: 80%;
  margin: 0 auto;
  border: none;
  display: block;
  font: inherit;
  background-color: #1f2021;
  color: #cccccc;
  padding-top: 20px;
  padding-bottom: 20px;
  padding-left: 30px;
}

.modal-form__form input::placeholder {
  color: #cccccc;
  font-size: 16px;
}

.breadcrumbs {
  margin: 0;
  margin-bottom: 75px;
  padding: 0;
  display: flex;
  align-items: baseline;
  width: 100%;
  padding-bottom: 10px;
  overflow: auto;
}

.breadcrumbs > * {
  flex-shrink: 0;
}

.breadcrumbs__item {
  font-size: 14px;
  padding-right: 5px;
}

.breadcrumbs__item:last-child a {
  color: #999999;
}

.breadcrumbs__item:last-child a:hover,
.breadcrumbs__item:last-child a:focus,
.breadcrumbs__item:last-child a:active {
  color: #fff;
  transition: color 0.5s;
}

.breadcrumbs__item + .breadcrumbs__item:before {
  content: '/';
  color: #fff;
}

.breadcrumbs__item a:hover,
.breadcrumbs__item a:focus,
.breadcrumbs__item a:active {
  color: #777;
  transition: color 0.5s;
}

.breadcrumbs__link {
  color: #fff;
}

/* WP Pagination */

.pagination.navigation {
